<template>
    <div class="page">
        <h1>{{ assetPack.name }}</h1>
        <span class="buttons">
            <button class="button" @click="importToProject">Import to Project</button>
            <button class="button" @click="edit">Edit</button>
            <button class="button danger" @click="deletePack">Delete</button>
            <button class="button" @click="$router.push('/')">Back</button>
        </span>

        <h3>Scenes</h3>
        <ul>
            <li v-for="(scene, index) in assetPack.scenes" :key="index">
                <input type="checkbox" :id="'scene' + index" :value="scene.name" v-model="selectedScenes"/>
                <label :for="'scene' + index">{{ scene.name }}</label>
            </li>
        </ul>
        <h3>Characters</h3>
        <ul>
            <li v-for="(character, index) in assetPack.characters" :key="index">
                <input type="checkbox" :id="'character' + index" :value="character.name" v-model="selectedCharacters"/>
                <label :for="'character' + index">{{ character.name }}</label>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            assetPack: {},
            selectedScenes: [],
            selectedCharacters: []
        }
    },
    mounted() {
        const assetId = this.$route.params.assetId;
        window.console.log(assetId);
        this.assetPack = this.$store.getters.assetPack(assetId);
    },
    methods: {
        importToProject() {
            const updates = {
                scenes: [],
                characters: []
            }
            for (var selectedScene of this.selectedScenes) {
                updates.scenes.push(this.assetPack.scenes.find(scene => scene.name === selectedScene));
            }
            for (var selectedCharacter of this.selectedCharacters) {
                updates.characters.push(this.assetPack.characters.find(character => character.name === selectedCharacter));
            }
            window.console.log("updating");
            window.console.log(updates);

            this.$store.commit("importAssetsToStory", updates);
            this.$router.push("/edit");
        },
        edit() {
            this.$router.push('/assets/update/' + this.assetPack.id);
        },
        deletePack() {
            this.$store.commit("deleteAssetPack", this.assetPack.id);
            this.$router.push("/");
        }
    }
}
</script>