<template>
    <div class="page">
        <h1>Story Maker</h1>
        <p>Loaded Story: {{ story.title }}</p>
        <button class="wideMenu button" @click="$router.push('/play')">Play</button>
        <button class="wideMenu button" @click="$router.push('/edit')">Edit</button>
        
        <hr />
        <UploadFile labelClass="wideMenu" v-model="importedStory" :text="true" id="uploadStory" label="Import" />
        <button class="wideMenu button" @click="exportStory">Export</button>

        <h2>Saved Stories</h2>
        <button class="wideMenu button" v-for="sty in stories" :key="sty.id" @click="loadStory(sty)">Load {{ sty.name }}</button>

        <h2>Asset Packs</h2>
        <button class="wideMenu button" @click="$router.push('/assets/update/new')">New</button>
        <button class="wideMenu button" v-for="pack in assetPacks" :key="pack.id" @click="$router.push('/assets/view/' + pack.id)">{{ pack.name }}</button>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";

export default {
    components: { UploadFile },
    data() {
        return {
            importedStory: {}
        }
    },
    computed: {
        story() {
            window.console.log("getting story");
            return this.$store.getters.story;
        },
        stories() {
            return this.$store.getters.stories;
        },
        assetPacks() {
            return this.$store.getters.assetPacks;
        }
    },
    watch: {
        importedStory(uploadedStory) {
            window.console.log("story imported");
            // this.story = JSON.parse(uloadedStory);
            this.$store.commit("update", JSON.parse(uploadedStory));
        }
    },
    methods: {
        loadStory(story) {
            this.$store.commit("load", story.id);
        },
        exportStory() {
            this.download(JSON.stringify(this.story), this.story.title.replace(/\s/g, ''), "json");
        },
        download(data, filename, type) {
            var file = new Blob([data], {type: type});
            if (window.navigator.msSaveOrOpenBlob) {// IE10+
                window.navigator.msSaveOrOpenBlob(file, filename);
            } else { // Others
                var a = document.createElement("a"),
                url = URL.createObjectURL(file);
                a.href = url;
                a.download = filename;
                document.body.appendChild(a);
                a.click();
                setTimeout(function() {
                    document.body.removeChild(a);
                    window.URL.revokeObjectURL(url);  
                }, 0); 
            }
        },
    }
}
</script>