
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

import { v4 as uuidv4 } from 'uuid';
import myLocalStorage from "@/myLocalStorage.js";

export default new Vuex.Store({
    state: {
        story: {
            id: uuidv4(),
            scenes: [],
            characters: [],
            plan: []
        },
        stories: myLocalStorage.loadStoryKeys(),
        assetPacks: myLocalStorage.loadAssetPackKeys()
    },
    getters: {
        story(state) {
            window.console.log("getting story in store");
            window.console.log("story is");
            window.console.log(state.story);
            return state.story;
        },
        stories(state) {
            return state.stories;
        },
        assetPacks() {
            return myLocalStorage.loadAssetPackKeys();
        },
        assetPack() {
            return id => myLocalStorage.loadAssetPack(id);
        }
    },
    mutations: {
        update(state, story) {
            window.console.log("updating story in store");
            state.story = story;
        },
        save(state, story) {       
            this.state.story = story;
            myLocalStorage.saveStory(state.story);
            state.stories = myLocalStorage.loadStoryKeys();
        },
        load(state, storyId) {
            state.story = myLocalStorage.loadStory(storyId);
        },
        updateAssetPack(state, assetPack) {
            myLocalStorage.saveAssetPack(assetPack);

        },
        importAssetsToStory(state, updates) {
            if (updates.scenes) {
                state.story.scenes = state.story.scenes.concat(updates.scenes);
            }
            if (updates.characters) {
                state.story.characters = state.story.characters.concat(updates.characters);
            }
        },
        deleteAssetPack(state, assetPackId) {
            myLocalStorage.deleteAssetPack(assetPackId);
        }
    }
});