<template>
    <div class="page">
        <h2>Scenes</h2>
        <span class="buttons">
            <button class="button" @click="addScene">New Scene</button>
            <button class="button" @click="$router.push('/edit')">Back</button>
        </span>
        <ul class="editlist box">
            <li v-for="scene in scenes" :key="scene.id">
                <input type="text" v-model="scene.name" placeholder="Name" />
                <img :src="scene.image" />
                <span class="buttons">
                    <UploadFile v-model="scene.image" :id="'uploadScene' + scene.id" />
                    <button class="button danger" @click="deleteScene(scene)">Delete</button>
                </span>
            </li>
        </ul>
    </div>
</template>

<script>
import UploadFile from "@/components/UploadFile.vue";
import { v4 as uuidv4 } from 'uuid';

export default {
    components: { UploadFile },
    data() {
        return {
            scenes: [],
            checkedScenes: []
        }
    },
    mounted() {
        this.scenes = this.$store.getters.story.scenes;
    },
    methods: {
        addScene() {
            this.scenes.push({
                id: uuidv4(),
                name: "",
                image: ""
            });
        },
        deleteScene(scene) {
            this.scenes.splice(this.scenes.indexOf(scene), 1);
        }
    }
}
</script>

<style scoped>
img {
    display: inline-block;
    max-height: 400px;
}
</style>