const STORY_KEYS = "stories";
const STORY = "story";
const ASSET_PACK = "assetPack";
const ASSET_PACK_KEYS = "assetPackKeys";

const getItem = function(key) {
    window.console.log("MyLocalStorage # [STORE] getItem (" + key + ")");
    const value = window.localStorage.getItem(key);
    window.console.log("RES: " + value);
    return JSON.parse(value);
}

const setItem = function(key, value) {
    window.console.log("MyLocalStorage # [STORE] setItem (" + key + ")");
    const stringValue = JSON.stringify(value);
    window.console.log("RES: " + stringValue);
    window.localStorage.setItem(key, stringValue);
}

const removeItem = function(key) {
    window.console.log("MyLocalStorage # [STORE] removeItem (" + key + ")");
    window.localStorage.removeItem(key);
}


export default {
    // STORIES

    loadStoryKeys() {
        window.console.log("MyLocalStorage # [story] loadStoryKeys");
        const savedStories = getItem(STORY_KEYS);
        if (!savedStories) {
            return [];
        } else {
            return savedStories;
        }
    },

    saveStoryKeys(storyKeys) {
        window.console.log("MyLocalStorage # [story] saveStoryKeys (storyKeys)");
        window.console.log(storyKeys);
        setItem(STORY_KEYS, storyKeys);
    },

    saveStory(story) {
        window.console.log("MyLocalStorage # [story] saveStory (story)");
        window.console.log(story);

        // Update story keys
        let storyKeys = this.loadStoryKeys();
        const updateStory = {id: story.id, name: story.title};
        const storyKeyIndex = storyKeys.findIndex(str => str.id === story.id);
        if (storyKeyIndex === -1) {
            storyKeys.push(updateStory);
            this.saveStoryKeys(storyKeys);
        } else {
            storyKeys.splice(storyKeyIndex, 1, updateStory);
        }
        this.saveStoryKeys(storyKeys);

        // Update story
        const key = STORY + "." + story.id;
        setItem(key, story);
    },

    loadStory(storyId) {
        window.console.log("MyLocalStorage # [story] loadStory (storyId)");
        window.console.log(storyId);
        const key = STORY + "." + storyId;
        return getItem(key);
    },

    // ASSET PACKS

    loadAssetPack(id) {
        window.console.log("MyLocalStorage # [assets] loadAssetPack (id)");
        window.console.log(id);
        const key = ASSET_PACK + "." + id;
        return getItem(key);
    },

    saveAssetPack(assetPack) {
        window.console.log("MyLocalStorage # [assets] saveAssetPack (assetPack)");
        window.console.log(assetPack);
        
        // update keys
        let loadAssetPackKeys = this.loadAssetPackKeys();
        const assetPackKeyIndex = loadAssetPackKeys.findIndex(ap => ap.id === assetPack.id);
        window.console.log("index is " + assetPackKeyIndex);
        const updateAssetPack = {id: assetPack.id, name: assetPack.name};
        if (assetPackKeyIndex === -1) {
            loadAssetPackKeys.push(updateAssetPack);
            this.saveAssetPackKeys(loadAssetPackKeys);
        } else {
            loadAssetPackKeys.splice(assetPackKeyIndex, 1, updateAssetPack);
        }
        this.saveAssetPackKeys(loadAssetPackKeys);

        // update pack
        const key = ASSET_PACK + "." + assetPack.id;
        setItem(key, assetPack);
    },

    loadAssetPackKeys() {
        window.console.log("MyLocalStorage # [assets] loadAssetPackKeys");
        let assetPackKeys = getItem(ASSET_PACK_KEYS);
        if (!assetPackKeys) {
            return [];
        } else {
            return assetPackKeys;
        }
    },

    saveAssetPackKeys(assetPackKeys) {
        window.console.log("MyLocalStorage # [assets] saveAssetPackKeys (assetPackKeys");
        window.console.log(assetPackKeys);
        setItem(ASSET_PACK_KEYS, assetPackKeys);
    },

    deleteAssetPack(assetPackId) {
        window.console.log("MyLocalStorage # [assets] deleteAssetPack (assetPackId)");
        window.console.log(assetPackId);
        let loadAssetPackKeys = this.loadAssetPackKeys();
        loadAssetPackKeys = loadAssetPackKeys.filter(key => key.id !== assetPackId);
        this.saveAssetPackKeys(loadAssetPackKeys);

        const key = ASSET_PACK + "." + assetPackId;
        removeItem(key);
    }
}