<template>
    <div class="page">
        <h1>Update Assets</h1>
        <span class="buttons">
            <button class="button" @click="save">Save</button>
            <button class="button" @click="$router.push('/')">Back</button>
        </span>

        <h2>Asset pack</h2>
        <span class="field">
            <label>Name</label>
            <input type="text" v-model="assetPack.name" />
        </span>

        <hr/>

        <h2>Current assets</h2>
        <button class="button danger" @click="removeAssets">Remove</button>
        <h3>Scenes</h3>
        <ul>
            <li v-for="(scene, index) in assetPack.scenes" :key="index">
                <input type="checkbox" :id="'scene' + index" :value="scene.name" v-model="selectedScenesToDelete"/>
                <label :for="'scene' + index">{{ scene.name }}</label>
            </li>
        </ul>
        <h3>Characters</h3>
        <ul>
            <li v-for="(character, index) in assetPack.characters" :key="index">
                <input type="checkbox" :id="'character' + index" :value="character.name" v-model="selectedCharactersToDelete"/>
                <label :for="'character' + index">{{ character.name }}</label>
            </li>
        </ul>

        <hr/>

        <h2>Add from existing project</h2>
        <button class="button" @click="addUpdateAssetPack">Add</button>
        <h3>Scenes</h3>
        <ul>
            <li v-for="(scene, index) in story.scenes" :key="index">
                <input type="checkbox" :id="'scene' + index" :value="scene.name" v-model="selectedScenes"/>
                <label :for="'scene' + index">{{ scene.name }}</label>
            </li>
        </ul>
        <h3>Characters</h3>
        <ul>
            <li v-for="(character, index) in story.characters" :key="index">
                <input type="checkbox" :id="'character' + index" :value="character.name" v-model="selectedCharacters"/>
                <label :for="'character' + index">{{ character.name }}</label>
            </li>
        </ul>

        
    </div>
</template>

<script>
import { v4 as uuidv4 } from 'uuid';

export default {
    data() {
        return {
            assetPack: {
                id: uuidv4(),
                scenes: [],
                characters: []
            },
            story: {},
            selectedScenesToDelete: [],
            selectedCharactersToDelete: [],
            selectedScenes: [],
            selectedCharacters: []
        }
    },
    mounted() {
        const assetId = this.$route.params.assetId;
        if (assetId !== "new") {
            this.assetPack = this.$store.getters.assetPack(assetId);
        }

        this.story = this.$store.getters.story;
    },
    computed: {
        hasAssetPack() {
            return Object.keys(this.assetPack).length > 0;
        }
    },
    methods: {
        createNew() {
            this.assetPack = {
                name: ""
            }
        },
        addUpdateAssetPack() {
            for (var selectedScene of this.selectedScenes) {
                this.assetPack.scenes.push(this.story.scenes.find(scene => scene.name === selectedScene));
            }
            for (var selectedCharacter of this.selectedCharacters) {
                this.assetPack.characters.push(this.story.characters.find(character => character.name === selectedCharacter));
            }
        },

        save() {
            this.$store.commit("updateAssetPack", this.assetPack);
            this.$router.push('/assets/view/' + this.assetPack.id);
        },

        removeAssets() {
            for (var scene of this.selectedScenesToDelete) {
                this.assetPack.scenes.splice(this.assetPack.scenes.indexOf(scene), 1);
            }
            for (var character of this.selectedCharactersToDelete) {
                this.assetPack.characters.splice(this.assetPack.characters.indexOf(character), 1);
            }
        }
    }
}
</script>